@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/vars/breakpoints.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.dd-switch-landing {
    $this: &;

    #{$this}__landing {
        .eureka-heading--h1 {
            font-size:
                var(
                    --eureka-core-typography-headings-level3-font-size,
                    #{eureka-light-theme(
                        'core-typography-headings-level3-font-size'
                    )}
                );
        }

        .eureka-callout--pad-content {
            padding: 0;
        }

        img {
            padding-top:
                #{eureka-to-rem(
                    eureka-light-theme('core-system-scale') * 14
                )};
        }
    }

    &__title {
        font-size:
            var(
                --eureka-core-typography-headings-level4-font-size,
                #{eureka-light-theme('core-typography-headings-level4-font-size')}
            );
        padding:
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 4)} 0
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0;
    }

    &__exclusive-perks-container--large-screen {
        @include eureka-flex-row;

        .eureka-callout--pad-content {
            padding: 0;
            width: 100%;
            max-width: 218px;
            text-align: left;

            .eureka-callout__icon-container {
                @include eureka-flex-row;
                justify-content: center;
                align-items: center;
                width:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 14
                    )};
                height:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 14
                    )};
                margin-bottom:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 4
                    )};
                background-color:
                    var(
                        --eureka-context-icon-primary-color-bg,
                        #{eureka-light-theme('context-icon-primary-color-bg')}
                    );
                border-radius: 50%;

                .eureka-callout--pad-content {
                    padding-right:
                        #{eureka-to-rem(
                            eureka-light-theme('core-system-scale') * 0.25
                        )};
                }
            }

            .eureka-callout__text-container {
                padding-right: 2rem;
                font-size:
                    var(
                        --eureka-core-typography-sub-text-font-size,
                        #{eureka-light-theme('core-typography-font-size')}
                    );

                .eureka-callout__msg-container {
                    margin-top:
                        #{eureka-to-rem(
                            eureka-light-theme('core-system-scale') * 1
                        )};
                }

                .eureka-callout__title,
                .eureka-callout__msg-container-inner {
                    font-size:
                        var(
                            --eureka-core-typography-sub-text-font-size,
                            #{eureka-light-theme('core-typography-font-size')}
                        );
                }
            }
        }
    }

    &__icon-money {
        width: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 9)};
        height: auto;
        color:
            var(
                --eureka-context-icon-primary-color,
                #{eureka-light-theme('context-icon-primary-color')}
            );
    }

    &__list-account-info {
        text-align: left;
        border:
            2px solid
            var(
                --eureka-core-color-canvas,
                #{eureka-light-theme('core-color-canvas')}
            );
        border-radius:
            #{eureka-to-px(
                eureka-light-theme('core-system-scale') * 2
            )};

        .eureka-list-item__text {
            font-size:
                var(
                    --eureka-core-typography-footnote-font-size,
                    #{eureka-light-theme('core-typography-footnote-font-size')}
                );
            color:
                var(
                    --eureka-core-color-text-alt,
                    #{eureka-light-theme('core-color-text-alt')}
                );
        }

        .eureka-list-item__meta {
            font-size:
                var(
                    --eureka-core-typography-large-font-size,
                    #{eureka-light-theme('core-typography-large-font-size')}
                );
            color:
                var(
                    --eureka-core-color-text,
                    #{eureka-light-theme('core-color-text')}
                );
        }

        .eureka-list-item__after-container {
            @include eureka-flex-row;
            justify-content: center;
            align-items: center;
            padding-right:
                #{eureka-to-rem(
                    eureka-light-theme('core-system-scale') * 5
                )};
            font-size:
                var(
                    --eureka-core-typography-sub-text-font-size,
                    #{eureka-light-theme('core-typography-font-size')}
                );
        }
    }

    &__exclusive-perks-container--small-screen {
        display: none;

        .eureka-accordion-item-heading {
            text-align: left;
        }

        .eureka-list-item {
            background-color: transparent;
        }

        .eureka-list__li + .eureka-list__li {
            border: none;
        }

        .eureka-accordion-item-panel {
            padding-top:
                #{eureka-to-rem(
                    eureka-light-theme('core-system-scale') * 4
                )};

            .eureka-list-item__text-container {
                text-align: left;
                padding-left:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 4
                    )};
                font-size:
                    var(
                        --eureka-core-typography-sub-text-font-size,
                        #{eureka-light-theme('core-typography-font-size')}
                    );

                .eureka-list-item__text {
                    font-weight:
                        var(
                            --eureka-core-typography-headings-font-weight,
                            #{eureka-light-theme(
                                'core-typography-headings-font-weight'
                            )}
                        );
                }
            }
        }

        .eureka-resource-list-item__icon-container {
            width: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 6)};
            height: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 6)};
            background-color:
                var(
                    --eureka-context-icon-primary-color-bg,
                    #{eureka-light-theme('context-icon-primary-color-bg')}
                );
            border-radius: 50%;
            box-sizing: content-box;

            #{$this}__icon-money {
                width:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 6
                    )};
            }
        }
    }

    @include eureka-breakpoint-max-only($eureka-s-min) {
        &__exclusive-perks-container--large-screen {
            display: none;
        }

        &__exclusive-perks-container--small-screen {
            display: block;
        }
    }

    &__banner-info {
        text-align: left;
        margin:
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 6)} 0 #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 4
            )} 0;

        .eureka-banner__icon-container {
            @include eureka-flex-row;
            justify-content: center;
        }

        #{$this}__icon-info {
            width: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 5)};
            height: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 5)};
            color:
                var(
                    --eureka-core-color-text-alt,
                    #{eureka-light-theme('core-color-text-alt')}
                );
        }
    }

    &__actions-container {
        @include eureka-flex-column;
        align-items: center;
        margin:
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 6)} 0 #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 6
            )} 0;

        #{$this}__button-download-dd-form {
            margin-bottom:
                #{eureka-to-rem(
                    eureka-light-theme('core-system-scale') * 2
                )};
        }
    }

    &__tips {
        p {
            font-size:
                var(
                    --eureka-core-typography-sub-text-font-size,
                    #{eureka-light-theme('core-typography-sub-text-font-size')}
                );
        }
    }
}
