@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/vars/breakpoints.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.dd-switch-bump {
    $this: &;
    text-align: left;

    &__container {
        .eureka-callout__inner {
            padding: 0;
            text-align: left;
        }
    }

    &__imgs-container {
        span {
            width: 56px;
            height: 56px;
            border: 1px solid #e6e6e6;
            border-radius: 50%;
            background-color:
                var(
                    --eureka-core-color-canvas,
                    eureka-light-theme('core-color-canvas')
                );
            display: inline-flex;
            justify-content: center;
            align-items: center;
        }

        img {
            width: 35px;
            height: 35px;
        }

        #{$this}__img-atomic {
            margin-left: -10px;
        }
    }

    &__title {
        padding:
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0;
    }

    &__subtitle {
        font-size:
            var(
                --eureka-core-typography-large-font-size,
                #{eureka-light-theme('core-typography-large-font-size')}
            );
        font-weight: bolder;
    }

    &__bump-content {
        padding-top:
            #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 2
            )};

        p {
            @include eureka-flex-row;
            align-items: center;

            img {
                padding-left:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 1
                    )};
                padding-right:
                    #{eureka-to-rem(
                        eureka-light-theme('core-system-scale') * 3
                    )};
            }
        }
    }

    &__actions-container {
        @include eureka-flex-row;
        justify-content: center;
        margin:
            #{eureka-to-rem(eureka-light-theme('core-system-scale') * 8)} 0 #{eureka-to-rem(
                eureka-light-theme('core-system-scale') * 6
            )} 0;
    }

    &__note {
        color:
            var(
                --eureka-core-color-text-alt,
                #{eureka-light-theme('core-color-text-alt')}
            );
        font-size:
            var(
                --eureka-core-typography-footnote-font-size,
                #{eureka-light-theme('core-typography-footnote-font-size')}
            );
    }
}

.dds-container {
    &__overlay-wrap {
        background-color:
            var(
                --eureka-core-color-grey-01,
                eureka-light-theme('core-color-grey-01')
            );
        height: 100%;
        left: 0;
        position: fixed;
        top: 0;
        width: 100vw;
    }

    &__page-wrap {
        background-color:
            var(
                --eureka-core-color-canvas,
                eureka-light-theme('core-color-canvas')
            );
        height: 80vh;
        left: 50%;
        margin: -40vh 0 0 -425px;
        overflow: hidden;
        position: absolute;
        top: 50%;
        width: 850px;
    }

    // Atomic official UI modal breakpoint 850px
    @include eureka-breakpoint-max-only(850px) {
        &__overlay-wrap {
            background-color:
                var(
                    --eureka-core-color-canvas,
                    eureka-light-theme('core-color-canvas')
                );
        }

        &__page-wrap {
            height: 100%;
            left: 0;
            margin: 0;
            top: 0;
            width: 100%;
        }
    }
}
